<template>
  <div class="login">
    <div class="login_main">
      <input
        class="login_main_input"
        v-model="token"
        placeholder="暗号"
        @input="checkToken"
      />
    </div>
  </div>
</template>
<script>
export default {
  name: "LoginView",
  data() {
    return {
      token: "",
      myToken: "yyds",
    };
  },
  methods: {
    checkToken() {
      if (this.token === this.myToken) {
        localStorage.setItem("RESUMETOKEN", this.myToken);
        //跳转主页
        this.$router.push("/resume");
      }
    },
  },
};
</script>
<style scoped>
.login {
  position: absolute;
  top: 30rem;
  bottom: 0;
  left: 0;
  right: 0;
}
.login_main {
  width: 150rem;
  height: 150rem;
  background: url("../assets/flower6.png");
  background-size: 100%;
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -75rem;
  margin-top: -75rem;
}
.login_main_input {
  background: rgb(255, 255, 255, 0.5);
  border-width: 0;
  width: 30rem;
  height: 8rem;
  font-size: 3rem;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: 5rem;
  padding-left: 1rem;
}
.login_main_input:focus {
  outline: none;
}
@media screen and (min-width: 1200px) {
  .login_main {
    width: 150rem;
    height: 150rem;
    background: url("../assets/flower6.png");
    background-size: 100%;
    background-repeat: no-repeat;
    position: relative; 
    top: 0;
    left: 0;
   
    margin:0 auto; 
  }
}
</style>